@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  background-image: url(/static/media/background.d3d8b78f.png);
  background-position: center;
  background-size: cover;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Chatbox container with solid blue color */
.chatbox-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px;
    background-color: #6965F1; 
    color: white; /* Text color for better contrast */
    width: 100%;
    max-width: none; /* Matches the max-width of MyChats */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15); /* Slight shadow for depth */
    position: relative;
    overflow: hidden; /* Ensures any overflowing content is clipped */
}

/* Buttons and inputs inside the chatbox */
.chatbox-container button,
.chatbox-container input {
    background-color: white; /* White background for contrast */
    border: 1px solid #ccc; /* Subtle border for definition */
    border-radius: 4px;
    color: black; /* Black text for better readability */
    padding: 10px 15px;
    margin: 5px 0;
    cursor: pointer;
    font-family: 'Work Sans', sans-serif;
}

/* Optional: Add hover effects for buttons */
.chatbox-container button:hover {
    background-color: #2B4274; /* Darker blue shade for hover */
    color: white; /* Text changes to white for contrast */
}


/* Style for chatbox text content */
.chatbox-container .chat-content {
    width: 100%;
    max-height: 600px; /* Limit height for large chats */
    overflow-y: auto; /* Add scrolling for long content */
    background-color: #f4f4f4; /* Light gray for content background */
    border-radius: 8px;
    padding: 15px;
    margin-top: 10px;
    color: black; /* Adjust text color for readability */
}

/* Chat messages inside the container */
.chatbox-container .chat-message {
    padding: 10px;
    border-radius: 6px;
    margin: 5px 0;
    max-width: 75%; /* Limit message width */
    word-wrap: break-word; /* Ensure long messages wrap properly */
}

/* Sender message */
.chatbox-container .chat-message.sender {
    background-color: #AAE7FF; /* Light blue for sent messages */
    color: black;
    -webkit-align-self: flex-end;
            align-self: flex-end; /* Align sender's messages to the right */
}

/* Receiver message */
.chatbox-container .chat-message.receiver {
    background-color: white; /* White for received messages */
    color: black;
    -webkit-align-self: flex-start;
            align-self: flex-start; /* Align receiver's messages to the left */
}

/* SingleChat.css */

/* Container for SingleChat */
.single-chat-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding: 3%;
    background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent white background */
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px); /* Apply blur effect */
    width: 100%;
    max-width: 700px; /* Adjust as needed */
    border-radius: 8px;
    border: 1px solid #ddd;
    position: relative;
    z-index: 1; /* Ensure it stays above other elements */
  }
  
  /* Chat Header */
  .single-chat-header {
    padding-bottom: 10px;
    padding-left: 10px;
    font-size: 30px;
    font-family: 'Work sans', sans-serif;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
  
  /* New Group Chat button */
  .new-group-btn {
    display: -webkit-flex;
    display: flex;
    font-size: 10px;
    padding: 5px;
    border-radius: 6px;
    background-color: #5966b7;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  /* Chat list container */
  .single-chat-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 12px;
    background-color: rgba(248, 248, 248, 0.8); /* Semi-transparent light gray background */
    width: 100%;
    height: 100%;
    border-radius: 6px;
    overflow-y: hidden;
  }
  
  /* Individual message item */
  .message-item {
    cursor: pointer;
    padding: 10px;
    border-radius: 6px;
    margin: 6px 0;
    background-color: #e8e8e8;
    transition: background-color 0.3s;
  }
  
  .message-item.selected {
    background-color: #5966b7;
    color: white;
  }
  
  /* Latest message preview */
  .latest-message {
    font-size: 12px;
    margin-top: 5px;
  }
  
  .latest-message b {
    color: #333;
  }
  
  /* Message input field */
  .single-chat-input {
    margin-top: 20px;
    background-color: rgba(240, 240, 240, 0.8); /* Transparent background for input */
    border-radius: 6px;
    border: none;
  }
  
  .single-chat-input input {
    background-color: #e0e0e0;
    padding: 10px;
    border-radius: 6px;
  }
  
/* MyChats.css */

