  font-family: 'Work Sans', sans-serif;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

/* New Group Chat button */
.new-group-btn {
  font-size: 0.9rem;
  padding: 8px 12px;
  border-radius: 6px;
  color: #5966b7;
  border: none;
  cursor: pointer;
}

.new-group-btn:hover {
  background-color: #4753a5;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

/* Chat list container */
.my-chats-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  max-height: 500px; /* Prevent the list from overflowing */
  overflow-y: auto;
  grid-gap: 8px;
  gap: 8px; /* Add spacing between items */
}

/* Individual chat item */
.chat-item {
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 6px;
  background-color: #e8e8e8;
  transition: background-color 0.3s, color 0.3s;
}

